import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { AppConstants, ArticleType } from "./appConstants";
import { environment } from '../../environments/environment';

@Injectable()
export class FirebaseDynamicLinkService {

    private template = `https://mycitykherson.page.link/?link=https://kherson-city.web.app/%23/dashboard&apn=com.mycity.kherson`;

    private subdomain = `https://mycitykherson.page.link`;

    private test = "test";

    constructor(private http: HttpClient) {
    }

    public getDynamicLink(articleId: string, type: ArticleType): Observable<any> {
        let base64Data = this.requestParamsToBase64(articleId, type);
        let requestModel = this.getRequestModel(base64Data);
        return this.http.post<any>(`https://firebasedynamiclinks.googleapis.com/v1/shortLinks?key=${environment.firebase.apiKey}`, requestModel);
    }

    private getRequestModel(base64Data: string): any {
        return {
            dynamicLinkInfo: {
                domainUriPrefix: "https://mycitykherson.page.link",
                link: `https://mycitykherson.page.link/${base64Data}`,
                androidInfo: {
                    "androidPackageName": "com.mycity.kherson"
                },
                iosInfo: {
                    "iosBundleId": "com.mycity.kherson"
                }
            }
        };

    }

    private requestParamsToBase64(id: string, type: string) {
        const model = {
            id,
            type
        };
        return btoa(JSON.stringify(model))
    }
}

export class CreateDynamicLinkRequest {
    public dynamicLinkInfo: any;
}
