export class DataProviderExtension {

    public static monthNamesEN: any = ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
    ];

    public static monthNamesUK: any = ["c—ñ—á–Ω—è", "–ª—é—Ç–æ–≥–æ", "–±–µ—Ä–µ–∑–Ω—è", "–∫–≤—ñ—Ç–Ω—è", "—Ç—Ä–∞–≤–Ω—è", "—á–µ—Ä–≤–Ω—è", "–ª–∏–ø–Ω—è",
        "—Å–µ—Ä–ø–Ω—è", "–≤–µ—Ä–µ—Å–Ω—è", "–∂–æ–≤—Ç–Ω—è", "–ª–∏—Å—Ç–æ–ø–∞–¥–∞", "–≥—Ä—É–¥–Ω—è"];

    public static dateTimeToUkrainianString(miliseconds: number) {
        let d = new Date(miliseconds*1000);
        let cDay = d.getDate();
        let cYear = d.getFullYear();
        let cMonth = this.monthNamesUK[d.getMonth()];

        return `${cDay} ${cMonth} ${cYear}`
    }
} 