import { MediaMatcher } from '@angular/cdk/layout';
import { ChangeDetectorRef, Component, OnDestroy, AfterViewInit, OnInit } from '@angular/core';
import { MenuItems } from '../../shared/menu-items/menu-items';
import * as $ from 'jquery';
import { NavigationEnd, Router } from '@angular/router';

/** @title Responsive sidenav */
@Component({
  selector: 'app-full-layout',
  templateUrl: 'full.component.html',
  styleUrls: ['full.component.scss']
})
export class FullComponent implements OnDestroy, OnInit {
  mobileQuery: MediaQueryList;
  public testValue: any = "testValue";
  private _mobileQueryListener: () => void;

  public showFooter: boolean = true;

  constructor(
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    public menuItems: MenuItems,
    private _router: Router
  ) {
    this.mobileQuery = media.matchMedia('(min-width: 768px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnInit() {
    this.handleFooter(this._router.url);

    this._router.events.subscribe((route) => {
      if (route instanceof NavigationEnd) {
        $(window).scrollTop(0);
        console.log(route.url);
        this.handleFooter(route.url);
    }});
  }

  handleFooter(route: string){
    if (route !== "/dashboard")
    this.showFooter = false;
  else
    this.showFooter = true;
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }
}
